import React from 'react';
import { CSSTransition } from 'react-transition-group';
import { Link } from 'react-scroll';
import subItems from './sub-items';
import style from './sub-menu.module.css';
import slide from '../../../../transitions/slide-side.module.css';

const SubMenu = ({ subOpen }) => (
  <CSSTransition in={subOpen} timeout={200} classNames={slide} unmountOnExit>
    <ul className={style.ul}>
      {subItems.map(el => (
        <li className={style.li} key={el.name}>
          {/* <a href={el.href}>{el.name}</a> */}
          <Link className={style.link} to={el.href} spy smooth duration={500}>
            {el.name}
          </Link>
        </li>
      ))}
    </ul>
  </CSSTransition>
);

export default SubMenu;
