import React, { Fragment } from 'react';
import Media from 'react-media';
import MobileWithoutOPS from './mobileWithoutOPS';
import DeskWithOPS from './deskWithOPS';
import ScrollUp from './Components/scrollUpButton/scrollUpButton';

const App = () => (
  <Fragment>
    <Media query={{ maxWidth: 1199 }}>
      {matches => (matches ? <MobileWithoutOPS /> : <DeskWithOPS />)}
    </Media>
    <ScrollUp />
  </Fragment>
);
export default App;
