import React, { Component, Fragment } from 'react';
import { Input } from 'semantic-ui-react';
import style from './contactsForm.module.css';
import submit from './submit';

export default class ConatactsForm extends Component {
  state = {
    name: '',
    phoneNumber: '',
    email: '',
  };

  onHandleChange = e => {
    const { target } = e;
    this.setState({ [target.name]: target.value });
  };

  onHandleSubmit = e => {
    e.preventDefault();
    submit(this.state);
    this.setState({ name: '', phoneNumber: '', email: '' });
  };

  render() {
    const { name, phoneNumber, email } = this.state;
    return (
      <Fragment>
        <h3 className={style.header}>связаться с нами</h3>
        <form className={style.form} onSubmit={this.onHandleSubmit}>
          <Input
            name="name"
            onChange={this.onHandleChange}
            focus
            type="text"
            placeholder="Имя"
            value={name}
            fluid
          />
          <Input
            name="phoneNumber"
            onChange={this.onHandleChange}
            focus
            type="tel"
            placeholder="Телефон"
            value={phoneNumber}
            fluid
          />
          <Input
            name="email"
            onChange={this.onHandleChange}
            focus
            type="email"
            placeholder="Email"
            value={email}
            fluid
          />
          <input className={style.submit} type="submit" value="Отправить" />
        </form>
      </Fragment>
    );
  }
}
