import React, { Fragment } from 'react';
import Media from 'react-media';
import style from './seventh-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import callCenterImg from '../../img/infoImg/callCenter.png';
import seventhScreenSomethinImg from '../../img/background/seventhScreenSomethingDesk.png';
import seventhScreenSomethin2Img from '../../img/background/seventhScreenSomethingDesk2.png';

const SeventhSection = () => (
  <section id="CallCenter" name="CallCenter" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={6} />
          </div>
        )
      }
    </Media>
    <Information />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <Fragment>
            <img className={style.img} alt="Office" src={callCenterImg} />
            <img
              className={style.something1}
              alt="back1"
              src={seventhScreenSomethinImg}
            />
            <img
              className={style.something2}
              alt="back2"
              src={seventhScreenSomethin2Img}
            />
          </Fragment>
        )
      }
    </Media>
  </section>
);

export default SeventhSection;
